<template>
	<div>
		<h2>图片</h2>
    <el-row :gutter="20">
      <el-col :xs="12" :sm="8" class="date-selector">
  		  <el-date-picker
  		    v-model="dateValue"
  		    type="date"
  		    placeholder="日期"
  		    :picker-options="disableFutureDays">
  		  </el-date-picker>
  		</el-col>
      <el-col :xs="12" :sm="8" class="type-selector">
        <el-select v-model="typeValue" placeholder="类型">
        	<el-option v-for="t in types" :value="t"></el-option>
        </el-select>
      </el-col>
      <el-col :xs="12" :sm="8" class="site-selector">
        <el-select v-model="siteValue" placeholder="站子">
        	<el-option v-for="s in sites" :value="s"></el-option>
        </el-select>
      </el-col>
  	</el-row>
	</div>
</template>

<script>
export default {
  data () {
    return {
      sites: [
        'kaleidoscope_BN',
        'BONAPICS0819',
        'Rabbit_950819',
        'redballoon_95',
        'Flawless_L0819'
      ],
      siteValue: '',
      types: ['预览', '高清', '官方图'],
      typeValue: '',
      dateValue: '',
      disableFutureDays: {
        disabledDate (time) {
          return time.getTime() > Date.now() - 8.64e7
        }
      }
    }
  }
}
</script>

<style>
  .el-col {
    padding-bottom: 10px;
  }
</style>



