<template>
  <div class="article">
    <el-card :body-style="{ padding: '2px' }" v-for="activity in activities">
      <div style="padding: 14px;">
        <h3>{{ activity.time + " " + activity.name }}</h3>
        <div class="bottom clearfix">
          <el-button type="text" class="button">更多</el-button>
        </div>
      </div>
      <div class="block">
        <el-carousel trigger="click">
          <el-carousel-item v-for="pic in activity.pics">
            <img :src="pic" class="image">
          </el-carousel-item>
        </el-carousel>
      </div>
    </el-card>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        activities: []
      }
    },
    mounted () {
      axios.get('static/api/activities.json').then(response => {
        var activities = response.data.activities
        var imgs = []
        for (var i = 0; i < activities.length; i++) {
          if (activities[i].pics[0].urls.length <= 3) {
            imgs = activities[i].pics[0].urls
            activities[i].pics = imgs
          } else {
            imgs = activities[i].pics[0].urls.slice(0, 3)
            activities[i].pics = imgs
          }
        }
        this.activities = activities
      })
    }
  }
</script>

<style>
  .article {
    padding: 20px;
  }

  .el-card {
    display: inline-block;
    vertical-align: top;
    margin-left: 16px;
    margin-bottom: 32px;
    margin-top: 0;
    width: 320px;
  }

  .el-carousel {
    z-index: 0;
    width: 300px;
    height: 200px;
    padding-left: 8px;
  }

  .el-carousel__item h3 {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 150px;
  margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
     background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n+1) {
     background-color: #d3dce6;
  }

  @media screen and (max-device-width: 768px) {
    h1 {
      font-size: 5em
    }
    h2 {
      font-size: 3em
    }
    .el-card {
      width: 600px;
      height: 400px;
    }
    .el-carousel {
      width: 600px;
      height: 400px;
    }
    .article {
      text-align: center;
      margin: 0 auto;
    }
  }
</style>



